//==============Variables==============
$bgColor: #1b1b20;
$bgInnerColor1: #31313aea;
$bgInnerColor2: #2a2a31ec;
$textColor: #e0e0e2;
$textShadowColor: rgb(175, 175, 175);
$lineColor: linear-gradient(90deg, #fffefe08, #8c8c8c9a, #fffefe08);
$mainColor: #ff9800;
$transparentMainColor: #ff990077;

//==============BreakPoints==============
@mixin xs-screen {
  @media (max-width: 575.9px) {
    @content;
  }
}

@mixin sm-screen {
  @media (max-width: 767.9px) {
    @content;
  }
}

@mixin md-screen {
  @media (max-width: 991.9px) {
    @content;
  }
}

@mixin lg-screen {
  @media (max-width: 1199.9px) {
    @content;
  }
}

@mixin xl-screen {
  @media (min-width: 1200px) {
    @content;
  }
}

//==============Placeholders==============
%shadow_line {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 1px;
  background-image: $lineColor;
}

//==============Components==============
.section-header {
  position: relative;
  padding: 25px;
  letter-spacing: 2px;
  .colored {
    position: relative;
    color: $mainColor;
    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: -10px;
      width: 30px;
      height: 30px;
      border-radius: 50%;
      background: linear-gradient(
        90deg,
        $transparentMainColor,
        rgba(0, 0, 0, 0.164)
      );
    }
  }
  &::after {
    @extend %shadow_line;
  }
}

.fade-in {
  animation: fadeIn 0.7s ease;
}

.fade-out {
  animation: fadeOut 0.7s ease;
}

//==============Animations==============
@keyframes fadeIn {
  0% {
    opacity: 0;
    margin-left: -300px;
  }
  100% {
    opacity: 1;
    margin-left: 0;
  }
}

@keyframes fadeOut {
  0% {
    opacity: 1;
    margin-left: 0;
  }
  100% {
    opacity: 0;
    margin-left: -300px;
  }
}

//==============Normalization==============
* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: Poppins;
}

::-webkit-scrollbar {
  width: 5px;
}

::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 50px;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}
