@font-face {
  font-family: "Poppins";
  src: url("../../assets/fonts/poppin/Poppins-Regular.eot");
  src: url("../../assets/fonts/poppin/Poppins-Regular.eot?#iefix")
      format("embedded-opentype"),
    url("../../assets/fonts/poppin/Poppins-Regular.woff2") format("woff2"),
    url("../../assets/fonts/poppin/Poppins-Regular.woff") format("woff"),
    url("../../assets/fonts/poppin/Poppins-Regular.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Poppins";
  src: url("../../assets/fonts/poppin/Poppins-ExtraLight.eot");
  src: url("../../assets/fonts/poppin/Poppins-ExtraLight.eot?#iefix")
      format("embedded-opentype"),
    url("../../assets/fonts/poppin/Poppins-ExtraLight.woff2") format("woff2"),
    url("../../assets/fonts/poppin/Poppins-ExtraLight.woff") format("woff"),
    url("../../assets/fonts/poppin/Poppins-ExtraLight.ttf") format("truetype");
  font-weight: 200;
  font-style: normal;
  font-display: swap;
}
